import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import NativeSelect from "@mui/material/NativeSelect";

const dispatchedEvents = [];

const SrsMuiNativeSelect = function (props, ref) {
  return (
    <NativeSelect {...props} theme={props.muiTheme}>
      <slot />
    </NativeSelect>
  );
};

const styles = `
  /* Custom styles for the MuiNativeSelect component */
`;

const props = [
  {
    name: "value",
    defaultValue: "",
    type: "string",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "onChange",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to NativeSelect if needed
];

const slots = [
  {
    name: "default",
    allowed: ["option", "optgroup"],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiNativeSelect),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
